.error {
  color: red;
  font-weight: 700;
}

.cat-info {
  gap: 20px;
  margin-top: 10px;
  display: flex;
}

.cat-desc {
  max-width: 600px;
}

.cat-img {
  border-radius: 5px;
}

.breed-select {
  margin-bottom: 10px;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 3px solid #fff;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: relative;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 56px;
  height: 56px;
  border: 3px solid rgba(0, 0, 0, 0);
  border-bottom-color: #ff3d00;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.is-hidden {
  display: none;
}

